<template>
    <div class="layout">
        <Layout>
            <my-head @back="bacl"></my-head>
            <Content :style="{padding: '0 50px'}">


                <Card style="padding: 0;margin-top: 30px;min-height: 500px;">
                    <div style="float: left;margin-right: 50px">

                        <router-link to="Take" style="color: #495060;width:220px">
                            <Card style="width:220px">
                                <div style="text-align:center;cursor: pointer;">
                                    <Icon type="android-list" size="120"></Icon>
                                    <h3>收货管理</h3>
                                </div>
                            </Card>
                        </router-link>
                    </div>

                    <div style="float: left;margin-right: 50px">
                        <router-link to="Inco" style="color: #495060;">
                            <Card style="width:220px">
                                <div style="text-align:center;cursor: pointer;">
                                    <Icon type="archive" size="120"></Icon>
                                    <h3>入库管理</h3>
                                </div>
                            </Card>
                        </router-link>
                    </div>

                    <div style="float: left;">
                        <!--<router-link style="color: #495060;">-->
                            <Card style="width:220px">
                                <div style="text-align:center;cursor: pointer;">
                                        <Icon type="android-open" size="120"></Icon>
                                    <h3>出库管理</h3>
                                </div>
                            </Card>
                        <!--</router-link>-->
                    </div>
                </Card>
            </Content>
            <Footer class="layout-footer-center" style="text-align: center">2011-2018 &copy; 怡乐仓库</Footer>
        </Layout>
    </div>
</template>
<script>
    export default {
        name: '',
        data () {
            return {
                user:{}
            }
        },
        methods: {
            bacl(i){
                this.user =i
            }
        },
        mounted(){
        }
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style scoped lang="less">
</style>



