<template>
    <div class="layout">
        <Layout>
            <my-head @back="bacl"></my-head>
            <Content :style="{padding: '0 50px'}">
                <Row type="flex" justify="center" align="middle" class="code-row-bg">

                    <Col span="20" push="4" type="flex" justify="end">
                    <span v-text="'单据号：'+$route.query.associate_n" style="float: right;margin-right: 20px;font-size: 14px"></span>
                    </Col>
                    <Col span="4" pull="20">
                    <Breadcrumb :style="{margin: '20px 0'}">
                        <BreadcrumbItem to="/Take">收货单</BreadcrumbItem>
                        <BreadcrumbItem :to="path">产品详情</BreadcrumbItem>
                        <BreadcrumbItem>拍照留档</BreadcrumbItem>
                    </Breadcrumb>
                    </Col>
                </Row>
                <Card style="padding: 0">

                    <Row style=" margin-bottom: 50px;">
                        <Col span="3">
                        <Upload
                                type="drag"
                                :before-upload="uploada"
                                accept="image/jpg,image/png,image/gif"
                                action="//jsonplaceholder.typicode.com/posts/"
                                style="width: 180px;">
                            <div style="padding: 25px 0;height: 140px;">
                                <Icon type="ios-camera" size="62" style="color: #495060"></Icon>
                                <p>凭证留档照（5张）</p>
                            </div>
                        </Upload>
                        </Col>
                        <Col span="19">
                        <div class="img" v-for="(item,index) in imglist">
                            <div class="imgdiv" @click="imgcli(index,1)">
                                <img :src="item.photoPath" alt="">
                            </div>
                            <div @click="removimg(item.serial,item.photo_type)">
                                <Icon type="android-delete" size="20"
                                      style="position:absolute;bottom:-4px;right:-25px;cursor: pointer;"></Icon>
                            </div>
                        </div>
                        </Col>
                    </Row>
                    <Row style=" margin-bottom: 50px;">
                        <Col span="3">
                        <Upload
                                multiple
                                type="drag"
                                :before-upload="uploadb"
                                accept="image/jpg,image/png,image/gif"
                                action="//jsonplaceholder.typicode.com/posts/"
                                style="width: 180px;">
                            <div style="padding: 25px 0;height: 140px;">
                                <Icon type="ios-camera" size="62" style="color: #495060"></Icon>
                                <!--<Icon type="ios-cloud-upload"></Icon>-->
                                <p>货品留档照（5张）</p>
                            </div>
                        </Upload>
                        </Col>
                        <Col span="19">
                        <div class="img" v-for="(item,index) in imgliss">
                            <div class="imgdiv" @click="imgcli(index,2)">
                                <img :src="item.photoPath" alt="">
                            </div>
                            <div @click="removimg(item.serial,item.photo_type)">
                                <Icon type="android-delete" size="20"
                                      style="position:absolute;bottom:-4px;right:-25px;cursor: pointer;"></Icon>
                            </div>
                        </div>
                        </Col>
                    </Row>
                    <Row type="flex" justify="end" class="code-row-bg">
                        <router-link :to="{path:'TakeVou',query:{associate_n:$route.query.associate_n}}"
                                     style="color: #fff;float: right;
                                 height: 50px;">
                            <Button type="primary" style="padding-left: 40px;padding-right: 40px;margin-right: 30px">
                                确定
                            </Button>
                        </router-link>
                    </Row>
                </Card>
            </Content>
            <Footer class="layout-footer-center" style="text-align: center">2011-2018 &copy; 怡乐仓库</Footer>
        </Layout>
        <Modal
                v-model="modal1"
                title="您已选中以下图片"
                :styles="{width:'450px'}">
            <div class="swiper-container">
                <div class="swiper-wrapper" v-if="img==1">
                    <div class="swiper-slide" v-for="(item,index) in imglist">
                        <div class="swiper-zoom-container">
                            <img :src="item.photoPath">
                        </div>
                    </div>
                </div>
                <div class="swiper-wrapper" v-else="">
                    <div class="swiper-slide" v-for="(item,index) in imgliss">
                        <div class="swiper-zoom-container">
                            <img :src="item.photoPath">
                        </div>
                    </div>
                </div>
                <!-- 如果需要分页器 -->
                <div class="swiper-pagination"></div>
            </div>
        </Modal>
    </div>
</template>
<script>
    import Swiper from 'swiper/dist/js/swiper.min.js'
    import 'swiper/dist/css/swiper.min.css'
    export default {
        name: '',
        data () {
            return {
                user: {},
                path: '/TakeVou?associate_n=' + this.$route.query.associate_n,
                img: 1,
                imglist: [],
                imgliss: [],
                modal1: false,
                modeimg: ''
            }
        },
        methods: {
            imgcli(i, type){
                this.img = type;
                this.modal1 = true;
                this.modeimg = i;
                var mySwiper = new Swiper('.swiper-container', {
                    zoom: true,
                    observer: true,
                    observeParents: true,
                    keyboard: true,
                    mousewheel: true,
                    initialSlide: i,
                    // 如果需要分页器
                    pagination: {
                        el: '.swiper-pagination',
                        type: 'fraction'
                    }
                })
            },

            //删除图片
            removimg(i, a){
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/delPhoto', v.Qs.stringify({
                    associate_no: v.$route.query.associate_n,
                    sheet_type: '送货单',
                    photo_type: a,
                    serial: i
                })).then(function (res) {
                    v.GetPhoto(a);
                    v.$Message.success('删除成功')

                });
            },

            //获取图片
            GetPhoto(num){
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/GetPhoto', v.Qs.stringify({
                    associate_no: v.$route.query.associate_n,
                    sheet_type: '送货单',
                    photo_type: num,
                    serial: -1,
                })).then(function (res) {
                    if (num == 1) {
                        v.imglist = res.data.data;
                    } else if (num == 2) {
                        v.imgliss = res.data.data
                    }

                });
            },

            //凭证留档照
            uploada(file){
                this.imglist == null || this.imglist.length < 5 ? this.upload(file, 1) : this.$emit('error', '您已上传5张')
            },

            //货品留档照
            uploadb(file){
                this.imgliss == null || this.imgliss.length < 5 ? this.upload(file, 2) : this.$emit('error', '您已上传5张')
            },

            //上传图片
            upload(file, num){
                var v = this;
                let reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onloadend = function (e) {
                    v.Axios.post('http://116.247.103.119/imt16webapi/sheets/PostSheetPhoto64', v.Qs.stringify({
                        associate_no: v.$route.query.associate_n,
                        sheet_type: '送货单',
                        photo_type: num,
                        serial: '',
                        oper_no: v.user.work_no,
                        photo: e.target.result.split(',')[1]
                    })).then(function (res) {
                        v.GetPhoto(num)
                    });
                };
            },

            bacl(i){
                this.user = i
            }
        },
        mounted(){
            this.GetPhoto(1);
            this.GetPhoto(2);
        }
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style scoped lang="less">
    .img {
        margin-bottom: 30px;
        float: left;
        position: relative;
        width: 140px;
        height: 140px;
        margin-left: 80px;
    }

    .img .imgdiv {
        position: relative;
        left: 0;
        top: 0;
        overflow: hidden;
        width: 140px;
        height: 140px;
    }

    .img img {
        position: absolute;
        width: 140px;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
    }


    .swiper-container {
        position: relative;
        padding: 30px 0;
        width: 400px;
        overflow: hidden;
    }
    .swiper-slide{
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
    }
    .swiper-zoom-container img{
        float: left;
        max-width:400px;
    }
</style>



