<template>
  <div id="app">
    <router-view @error="error"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data(){
    return{

    }
  },
  methods:{
    //全局状态
    error (msg) {
      this.$Message.error(msg);
    }

  },
  mounted(){
    this.$Message.config({
      top: 25,
      duration: 3
    });
  }
}
</script>


