import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import List from '@/components/List'

import Take from '@/components/Take'
import TakeVou from '@/components/TakeVou'
import TakePhoto from '@/components/TakePhoto'

import Inco from '@/components/Inco'
import IncoVou from '@/components/IncoVou'
import IncoPhoto from '@/components/IncoPhoto'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component:resolve => require(['@/components/Index'],resolve)
    },
    {
      path: '/List',
      name: 'List',
      component:resolve => require(['@/components/List'],resolve)
    },
    {
      path: '/Take',
      name: 'Take',
      component:resolve => require(['@/components/Take'],resolve)
    },
    {
      path: '/TakeVou',
      name: 'TakeVou',
      component:resolve => require(['@/components/TakeVou'],resolve)
    },
    {
      path: '/TakePhoto',
      name: 'TakePhoto',
      component:resolve => require(['@/components/TakePhoto'],resolve)
    },
    {
      path: '/Inco',
      name: 'Inco',
      component:resolve => require(['@/components/Inco'],resolve)
    },
    {
      path: '/IncoVou',
      name: 'IncoVou',
      component:resolve => require(['@/components/IncoVou'],resolve)
    },
    {
      path: '/IncoPhoto',
      name: 'IncoPhoto',
      component:resolve => require(['@/components/IncoPhoto'],resolve)
    }
  ]
})



