<template>
    <div class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <router-link to="List">
                        <div class="layout-logo">
                            <img src="../../assets/637211310743027966.png" alt="">
                        </div>
                    </router-link>
                    <div class="layout-nav wrapper-header-nav">
                        <MenuItem name="3" style="font-size: 14px;">
                            <Icon type="ios-paper"></Icon>
                            {{user.bookstore_name}}
                        </MenuItem>
                        <MenuItem name="2" style="font-size: 14px;">
                            <Icon type="social-octocat"></Icon>
                            {{user.chn_name}}
                        </MenuItem>
                        <MenuItem name="1" style="font-size: 14px;">
                            <p @click="unCookie">注销</p>
                        </MenuItem>
                    </div>
                </Menu>
            </Header>
        </Layout>
    </div>
</template>
<script>
    export default {
        name: '',
        data () {
            return {
                user:{}
            }
        },
        methods: {
            //清除cookie
            unCookie(){
                this.Yl.removeCookie('user');
                this.$router.push('/');
            },
            setUser:function(){}
        },
        beforeCreate(){
        },
        mounted(){

            var v = this;
            if (v.Yl.cookie('user') == undefined||v.Yl.cookie('user')=='') {
                v.$router.push('/')
            } else {
                v.user = JSON.parse(v.Yl.cookie('user'));
                v.$emit('back',v.user);
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style scoped lang="less">
    .layout {
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }

    .layout-logo {
        width: 100px;
        height: 30px;
        background: #5b6270;
        border-radius: 3px;
        float: left;
        position: relative;
        top: 15px;
        left: 20px;
    }
    .layout-logo img{
        float: left;
        margin-left: 5px;
        width: 90px;
        margin-top: 4px;
    }

    .layout-nav {
        float: right;
        text-align: right;
        margin: 0 auto;
    }

    .layout-footer-center {
        text-align: center;
    }
</style>



